.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.heart {
  color: #ff0000;
}

.small {
  font-size: 0.75rem;
}
.icons{
  display: none;
}
.nav{
  background-color: rgb(65,105,225);
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding-left: 10%;
  padding-right: 10%;
  height: 8vh;
  color: white;
}
#login{
  background-color: white;
  color:blue;
  border: white;
  width:125px;
  height:25px;
}
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
#dropdown{
  width: 80px;
  background-color: rgba(65,105,225,0.9);
  border: rgba(65,105,225,0.9);
}
#input{
  width: 300px;
  height: 30px;
}
#hide{
  display: flex;
  justify-content: space-between;
  width: 350px;
}
#hide p{
  padding-top: 13px;
}
.firstrow,.d{
  display: flex;
  justify-content: space-around;
  align-items: center;
  background-color: white;
}
.d{
  overflow: scroll;
  margin: 0px 15px;
}
body{
  background-color: rgba(220,220,220,0.5);
}  
.v{
  background-color: rgba(65,105,225,0.9);
  width: 100px;
  color: white;
  height: 35px;
  border: rgba(65,105,225,0.9);
}
.top-offers{
display: flex;
flex-direction: column;
margin-left: 0%;
align-items:flex-start;
justify-content: space-around;
margin: 10px;
}
.mobile{
  background-color: white;
}
#bank{
  margin: 0;
}
.top-offers p,button,img{
  margin: 10px;
}
.top-offers p{
  margin-top: 40px;
  font-size: 25px;
  margin-left: 40px;
}
.top-offers button{
  margin-left: 70px;
}

@media only screen and (max-width: 769px){
  #hide{
    display:flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    position: fixed;
    top:45px;
    background-color: rgba(65,105,225,0.9);
    width:200px;
    height: 50vh;
    padding: 20px 10px;
    right: -300px;
  }
  .nav{
    padding-left: 1%;
    padding-right: 1%;
    height: 8vh;
  }
  #input{
    width: 200px;
    height: 30px;
  }
  .icons{
    display: inline;
  }
  .footer{
    flex-wrap: wrap;
  }
  #hide p,select,p{
    padding: 20px;
  }
  #hide.active{
    right:0px;
  }
  .firstrow{
    flex-wrap: wrap;
  }
  #bank{
    height: 100px;
  }
  .mobile{
    flex-wrap: wrap;
  }
  .fifthrow{
    flex-wrap: wrap;
  }
  .fifthrow img{
    margin: auto;
    margin-bottom: 10px;
  }
  #b{
    margin-top: 320px;
    margin-bottom: 440px;
  }
  .d{
    flex-wrap: wrap;
    margin-top:400px;
  }
}
